<template>
	<div class="autocomplete">
		<input
			type="text"
			v-model="search"
			@input="onChange"
			placeholder="Search county"
			class="search-bar"
		/>
		<ul v-show="isOpen" class="autocomplete-results">
			<li
				v-for="(result, i) in results"
				:key="i"
				class="autocomplete-result"
				@click="setResult(result.county)"
			>
				{{ result.county }}
			</li>
		</ul>
	</div>
</template>

<script>
import { mapActions } from "vuex";

export default {
	name: "AutoComplete",
	props: {
		items: {
			type: Array,
			required: false,
			default: () => []
		}
	},
	data() {
		return {
			search: "",
			results: [],
			isOpen: false
		};
	},
	methods: {
		...mapActions(["setCounty"]),
		onChange() {
			if (this.search == "") {
				this.isOpen = false;
			} else {
				this.isOpen = true;
				this.filterResults();
			}
		},
		filterResults() {
			this.results = this.items.filter((item) => {
				return (
					item.county
						.toLowerCase()
						.indexOf(this.search.toLowerCase()) > -1
				);
			});
		},
		setResult(result) {
			this.setCounty(result);
			this.isOpen = false;
			this.search = result;
		}
	}
};
</script>

