import Vuex from "vuex";
import Vue from "vue";
import data from "@/data/sales_data.json";

Vue.use(Vuex);

const state = {
	selectedTab: 3,
	sales_data: [],
	selectedCounty: {}
};
const getters = {
	getSelectedTab(state) {
		return state.selectedTab;
	},
	getSalesData(state) {
		return state.sales_data;
	},
	getSelectedCounty(state) {
		return state.selectedCounty;
	}
};
const mutations = {
	changeTab(state, tab) {
		state.selectedTab = tab;
	},
	setData(state, data) {
		state.sales_data = data;
	},
	setSelectedCounty(state, county) {
		state.sales_data.forEach((el) => {
			if (el.county.toLowerCase() === county.toLowerCase()) {
				state.selectedCounty = el;
			} else {
				return false;
			}
		});
	}
};
const actions = {
	changeTab({ commit }, tab) {
		commit("changeTab", tab);
	},
	async fetchData({ commit }) {
		commit("setData", data);
		commit("setSelectedCounty", "London");
	},
	setCounty({ commit }, result) {
		commit("setSelectedCounty", result);
	}
};

export default new Vuex.Store({
	state,
	getters,
	mutations,
	actions
});
