<template>
	<div class="results-view">
		<div class="results-search">
			<div class="tab active">
				<div class="search-bar">
					<AutoComplete v-bind:items="salesData" />
				</div>
			</div>
		</div>
		<div class="results-content">
			<h2 class="selected-county">
				{{ selectedCounty.county | uppercase }}
			</h2>

			<div class="result-section house-sales-statistics">
				<div class="section-title">
					<span class="section-title-icon"
						><img class="img-fluid" src="@/assets/housesalesdc.svg"
					/></span>
					<h3 class="text-white">House Sales Enquiries:</h3>
				</div>
				<div class="section-content">
					<div class="house-sales">
						<span class="house-sales-result">
							<input
								class="result-input grow"
								type="text"
								:value="
									getPercentChange(
										selectedCounty.percent_change
									)
								"
							/>
							<span class="mx-2">by</span>
							<input
								class="result-input"
								type="text"
								:value="
									getPercentNumber(
										selectedCounty.percent_change
									)
								"
							/>
						</span>
					</div>
					<div class="content-sub-text text-center mt-2">
						comparing figures from Jan - Sep 2019 to 2020
					</div>
				</div>
			</div>

			<div
				class="result-section house-sales-demographics mt-4"
				v-if="selectedCounty.percent_change !== 'N/A'"
			>
				<div class="section-title">
					<span class="section-title-icon"
						><img
							class="img-fluid"
							src="@/assets/demographic_icon.svg"
					/></span>
					<h3 class="text-white">Most common type of seller:</h3>
				</div>
				<div class="section-content">
					<div class="sales-demographics">
						<span class="sales-demographics-result">
							<input
								class="result-input input-small"
								type="text"
								value="2019"
							/>
							<input
								class="result-input grow"
								type="text"
								:value="selectedCounty.commonsales20191"
							/>
						</span>
						<span class="sales-demographics-result">
							<input
								class="result-input input-small"
								type="text"
								value="2020"
							/>
							<input
								class="result-input grow"
								type="text"
								:value="selectedCounty.commonsales20201"
							/>
						</span>
					</div>
				</div>
			</div>

			<div class="result-section covid-statistics">
				<div class="section-title">
					<span class="section-title-icon"
						><img class="img-fluid" src="@/assets/virusdc.svg"
					/></span>
					<h3 class="text-white">COVID-19 STATS (JAN - SEP 2020)</h3>
				</div>
				<div class="section-content covid-section">
					<div v-if="selectedCounty.covid_level" class="covid-cases">
						<span class="input-subtitle"
							>Covid cases per 1000 people:</span
						>
						<span
							class="grade-result high active"
							v-show="selectedCounty.covid_level === 'High'"
							>{{ selectedCounty.per_capita_1000 }}</span
						>
						<span
							class="grade-result medium active"
							v-show="selectedCounty.covid_level === 'Medium'"
							>{{ selectedCounty.per_capita_1000 }}</span
						>
						<span
							class="grade-result low active"
							v-show="selectedCounty.covid_level === 'Low'"
							>{{ selectedCounty.per_capita_1000 }}</span
						>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { mapGetters } from "vuex";
import AutoComplete from "@/components/AutoComplete.vue";

export default {
	name: "ResultsView",
	components: {
		AutoComplete
	},
	methods: {
		getPercentChange(val) {
			if (val == "") {
				return "";
			} else {
				if (val.indexOf("-") != -1) {
					return "DECREASED";
				} else {
					return "INCREASED";
				}
			}
		},
		getPercentNumber(val) {
			if (val[0] === "-") {
				return val.substr(1);
			} else {
				return val;
			}
		},
		numberWithCommas(x) {
			return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
		}
	},
	computed: {
		...mapGetters({
			selectedTab: "getSelectedTab",
			salesData: "getSalesData",
			selectedCounty: "getSelectedCounty"
		}),
		covid_cases() {
			return this.numberWithCommas(this.selectedCounty.covid_cases);
		}
	},
	filters: {
		uppercase: (v) => {
			if (v) {
				return v.toUpperCase();
			} else {
				return "";
			}
		}
	}
};
</script>

